import Messager from '../messager';

if (typeof __self == 'undefined') {
  self = {
    fns: {},
    addEventListener(type, callback) {
      this.fns[type] = callback;
    },

    dispatchEvent(type, data) {
      this.fns[type].call(this, {
        data
      });
    },

    //native
    postMessage(data) {
      __nativeCall(data);
    }
  }
}

export default new Messager(self);